body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch__hand {
  z-index: -1;
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand--min {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite hand-animation;
}

.stopwatch__hand--sec {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite hand-animation;
}

.stopwatch--speed-up .stopwatch__hand--sec {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__hand--min {
  animation-duration: 600s;
}

@keyframes hand-animation {
  from {
    transform: rotateZ(0);
  }

  to {
    transform: rotateZ(360deg);
  }
}
